<template>
  <div class="askDetall">
    <mt-header :title="title">
      <mt-button  icon="back" slot="left" @click="handleBack">返回</mt-button>
    </mt-header>

    <section class="content" v-if="data">
      <h2>{{data.title}}</h2>
      <!-- <span class="user">
        <img :src="getUserAvatar" />
        <span>
          <em>{{data.trueName}}</em>
          <i>发布于 {{data.q_sendtime | formatDate}}</i>
        </span>
        <div v-if="!isFollow" @click="follow()" class="btn">+关注</div>
      </span> -->
      <p class="des"><i>摘要:</i>{{data.des}}</p>
      <article  class="lj-editor-show" v-html="data.content"></article>
      <!-- <p v-html="data.content"></p> -->
      <!-- <div class="q_img" v-if="data.imgs">
        <li v-for="item in getQimg" @click="showQImg(item)"><img :src="item"></li>
      </div> -->
      <div class="bottom">
        <!-- <span class="R"><em>{{data.trueName}}</em><i>|</i><em>{{data.q_sendtime | formatDate}}</em></span>
        <span class="follow">
          <em v-if="!isFollow"  @click="follow()" class="btn">
            关注<i>|</i>{{data.q_cang}}</em>
          <em v-if="isFollow">已关注<i>|</i>{{data.q_cang}}</em>
        </span>
        <span><img src="../../images/home/A_degree_icon.png"><em>{{data.q_replys}}</em></span>
        -->
        <!-- <span @click="gotoAnswer"><img src="../../images/home/A_degree_icon.png"><em>添加回答</em></span> -->
      </div>
    </section>
    <!-- 10线条 -->
    <div class="lise_10"></div>

    <!-- 65线条 -->
    <div v-if="false"  :class="isShowTool ? 'lise_240' : 'lise_40'"></div>
  </div>
</template>

<script>
import { Toast,Indicator,MessageBox} from 'mint-ui'
import {getStore,imgBaseUrl,upImgBaseUrl} from '../../components/common/mUtils'
import upload from '../../components/common/upload'
import AList from './children/AList'
export default {
  data(){
    return{
      imgBaseUrl,
      upImgBaseUrl,
      userToken:'',
      isLogin:false,
      data:null,
      searchStr:'',
      id:'',
      title:'',
      isShowTool:false,
      // 回答的参数
      replys:{
        nr:'',
        qid:'',
        imgs:'',
        img:[]
      },
      popImgUrl:'',
      isShowPopImg:false,
      isFollow:false,
    }
  },
  props:['itemData'],
  filters:{
    getAvatarPath(path){
      return '//img.07358.com'+path
    }
  },
  created(){
    this.userID = getStore('userID')
    this.userToken = getStore('userToken')
    if (this.userID) {
      this.isLogin = true
    }
  },
  mounted(){
    //this.userToken = 'MlDiUbv-FiE4eu23HIJ9u9pv9MwkqCmo'
    this.id = this.$route.query.id
    this.replys.qid = this.qid
    this.getData()
  },
  components:{
    upload,
    AList
  },
  computed:{
    getQimg(){
      let imgs = this.data.imgs; //这是一字符串
      return imgs.split(","); //字符分割
    },
    getUserAvatar:function(){
      if(this.data){
        if (this.data.avatarPath == '') {
          return "http://jimg.07358.com/Fnhc3IKMv02O5Udciy3gsPCf6C6y"
        }else {
          return imgBaseUrl + this.data.avatarPath
        }
      }else {
        return "http://jimg.07358.com/Fnhc3IKMv02O5Udciy3gsPCf6C6y"
      }
    }
  },
  methods: {
    handleBack(){
      this.$router.go(-1);
    },
    gotoSearch(){
      let str = encodeURI(encodeURI(this.searchStr));
      this.$router.push({path: '/search', query:{keyStr:str}})
    },
    showTool(){
      if (!this.validateLogin()) {
        return
      }
      this.isShowTool = !this.isShowTool
    },
    gotoAnswer(){
      this.$router.push({path:'answer',query:{qid:this.qid }})
    },
    getData(){
      // Indicator.open('加载中....');
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/showbk?id='+this.id
      // console.log(getUrl)
      this.axios.get(getUrl)
      .then(function (response) {
        let code = response.data.code
        if (code == 200) {
          this.data = response.data.result
          this.title = this.data.title
        }
        // Indicator.close();
      }.bind(this))
      .catch(function (error) {
        console.log(error);
      });
    },
  }

}

</script>

